@charset "UTF-8";
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /**
  * Correctif de la largeur de la légende d'un fieldset
  */
  .fr-pagination__list {
    list-style: none;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-pagination {
    color: #161616;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-pagination__link[aria-current] {
    background-color: #000091;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-pagination__link[aria-current]:hover {
    background-color: #1212ff;
  }
  .fr-pagination__link[aria-current]:active {
    background-color: #2323ff;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-pagination__link[aria-current] {
    color: #f5f5fe;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-pagination__link:not([aria-current]):disabled, a.fr-pagination__link:not([aria-current]):not([href]) {
    color: #929292;
  }
}
